          right: 1%;\r\n                top: 0;\r\n                font-weight:bold;\r\n                font-size: 1.5rem;\r\n                position: absolute;\r\n                &:hover, &:focus {\r\n                    color: $loadOrError;\r\n                    cursor: pointer;\r\n                }\r\n            }\r\n        }\r\n        .loading {\r\n            display: none;\r\n            width: 200px;\r\n            height: 200px;\r\n            position: absolute;\r\n            border-radius: 50%;\r\n            background: transparent;\r\n            border: 40px solid #f3f3f3;\r\n            border-top: 40px solid $loadOrError;\r\n            animation: load 1.2s ease-in-out infinite;\r\n        }\r\n        @keyframes load {\r\n            0% {\r\n                transform: rotate(0deg);\r\n              }\r\n              100% {\r\n                transform: rotate(360deg);\r\n              }\r\n            }\r\n        }\r\n    }\r\n    .content {\r\n        min-height: 100%;\r\n        min-width: 100%;\r\n        background: url('../assets/background.png') 0 no-repeat, $loadOrError;\r\n        background-size: cover;\r\n        background-position: center;\r\n        padding: 1em 4em;\r\n        \r\n        .mainContent {\r\n            min-height: 95vh;\r\n            min-width: 100%;\r\n            display: grid;\r\n            align-content: space-evenly;\r\n            grid-template-rows: fit-content 1fr;\r\n            .todayInfo {\r\n                display: grid;\r\n                grid-template-columns: 1fr 1fr;\r\n                margin-top: 5%;\r\n                .mainInfo {\r\n                    display: flex;\r\n                    flex-direction: column;\r\n                    .locationInfo {\r\n                        .name {\r\n                            font-size: 2rem;\r\n                        }\r\n                        .dayInfo, .currentTime {\r\n                            font-size: 1.5rem;\r\n                        }\r\n                    }\r\n                    .degreesInfo {\r\n                        .degreesDiv {\r\n                            display: flex;\r\n                            .degrees {\r\n                                font-size: 2.5rem;\r\n                            }\r\n                        }\r\n                        \r\n                        .weatherDescription {\r\n                            font-size: 1.5rem;\r\n                        }\r\n                        .search-box{\r\n                            margin-top: 10px;\r\n                            width: fit-content;\r\n                            height: fit-content;\r\n                            position:absolute;\r\n                            .input-search {\r\n                                height: 50px;\r\n                                width: 50px;\r\n                                border-style: none;\r\n                                padding: 10px;\r\n                                font-size: 18px;\r\n                                letter-spacing: 2px;\r\n                                outline: none;\r\n                                border-radius: 25px;\r\n                                transition: all .35s ease-in-out;\r\n                                background-color: rgba(0, 0, 0, 0.5);\r\n                                padding-right: 40px;\r\n                                color:#fff;\r\n                          }\r\n                            .input-search::placeholder {\r\n                                color:rgba(255,255,255,.5);\r\n                                font-size: 18px;\r\n                                letter-spacing: 2px;\r\n                                font-weight: 100;\r\n                            }\r\n                            .btn-search{\r\n                                width: 50px;\r\n                                height: 50px;\r\n                                border-style: none;\r\n                                font-size: 20px;\r\n                                font-weight: bold;\r\n                                outline: none;\r\n                                cursor: pointer;\r\n                                border-radius: 50%;\r\n                                position: absolute;\r\n                                right: 0px;\r\n                                color:#ffffff ;\r\n                                background-color:transparent;\r\n                                pointer-events: painted;\r\n                                display: flex;\r\n                                justify-content: center;\r\n                                align-items: center;\r\n                            }\r\n                            .btn-search:focus ~ .input-search{\r\n                                width: 300px;\r\n                                border-radius: 0px;\r\n                                background-color: transparent;\r\n                                border-bottom:1px solid rgba(255,255,255,.5);\r\n                                transition: all 350ms cubic-bezier(0, 0.110, 0.35, 2);\r\n                            }\r\n                            .input-search:focus{\r\n                                width: 300px;\r\n                                border-radius: 0px;\r\n                                background-color: transparent;\r\n                                border-bottom:1px solid rgba(255,255,255,.5);\r\n                                transition: all 350ms cubic-bezier(0, 0.110, 0.35, 2);\r\n                            }\r\n                        }\r\n                    }\r\n                }\r\n                .secondaryInfo {\r\n                    display: flex;\r\n                    flex-direction: column;\r\n                    align-items: flex-end;\r\n                    font-size: 1.1rem;\r\n                    gap: 5%;\r\n                    > div {\r\n                        text-align: center;\r\n                    }\r\n                